import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import News from './views/News.vue'
import Embajadores from './views/Embajadores.vue'
import Embajador from './views/Embajador.vue'
import Recursos from './views/Recursos.vue'
import Contacto from './views/Contacto.vue'
import Entrada from './views/Entrada.vue'
import Entrada2 from './views/Entrada2.vue'
import Entrada3 from './views/Entrada3.vue'
import Entrada4 from './views/Entrada4.vue'
import Mapa from './views/Mapa.vue'

Vue.use(Router)

export default new Router({
  scrollBehavior() {
    return { x: 0, y: 0 };
  },
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/News',
      name: 'News',
      component: News
    },
    {
      path: '/Embajadores',
      name: 'Embajadores',
      component: Embajadores
    },
    {
      path: '/Embajadores/Embajador',
      name: 'Embajador',
      component: Embajador
    },
    {
      path: '/Recursos',
      name: 'Recursos',
      component: Recursos
    },
    {
      path: '/Contacto',
      name: 'Contacto',
      component: Contacto
    },
    {
      path: '/Recursos/Entrada/1',
      name: 'Entrada',
      component: Entrada
    },
    {
      path: '/Recursos/Entrada/2',
      name: 'Entrada2',
      component: Entrada2
    },
    {
      path: '/Recursos/Entrada/3',
      name: 'Entrada3',
      component: Entrada3
    },
    {
      path: '/Recursos/Entrada/4',
      name: 'Entrada4',
      component: Entrada4
    },
    {
      path: '/Mapa',
      name: 'Mapa',
      component: Mapa
    },
  ]
})
