<script>
export default {
  name: 'Accordion',
  methods: {
    toogle(e) {
      console.log(e.target)
      e.target.classList.toggle("active")
      var panel = e.target.nextElementSibling
      if (panel.style.display === "block") {
        panel.style.display = "none"
      } else {
        panel.style.display = "block"
      }
    }
  }
}
</script>


