      </div>
    </div>
    
    <div class="controls">
      <div class="control--left animated" v-on:click="slide('prev', $event)">&lt;</div>
      <div class="control--right animated" v-on:click="slide('next', $event)">&gt;</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Slider',
  data: () => {
    return {
      position: 0
    }
  },
  methods: {
    slide (direction, e) {
      e.target.classList.remove('bounceIn')
      setTimeout(() => {
        e.target.classList.add('bounceIn')
      }, 1)
      const slideJump = 280
      let activeSlide = document.querySelector('.active')
      let allSlides = [...document.querySelectorAll('.slide')]

      switch (direction) {
        case 'next':
          if (activeSlide.nextSibling != null ) {
            activeSlide.classList.remove('active')
            activeSlide.nextSibling.classList.add('active')
            this.position = this.position - slideJump
            moveSlide(this.position)
          } else {
            limitSlide(activeSlide)
          }
          break;
        case 'prev':
          if (activeSlide.previousSibling != null ) {
            activeSlide.classList.remove('active')
            activeSlide.previousSibling.classList.add('active')
            this.position = this.position + slideJump
            moveSlide(this.position)
          } else {
            limitSlide(activeSlide)
          }
          break;
      }

      function moveSlide(position) {
        allSlides.map(slide => {
          slide.style.transform = `translateX(${position}px)`
        })
      }

      function limitSlide(slide) {
        let animationName = 'flash'
        slide.classList.add(animationName)
        slide.addEventListener('animationend', () => {
          slide.classList.remove(animationName)
        })
      }

    }
  }
}
</script>

