<script>
import { required, minLength, between, email, numeric } from 'vuelidate/lib/validators'
import Button from '@/components/Button.vue'
import Fab from '@/components/Fab.vue'

export default {
  components: {
    Button,
    Fab
  },
  data() {
    return {
      name: '',
      phone: '',
      mail: '',
      message: '',
      submitStatus: null
    }
  },
  validations: {
    name: {
      required,
      minLength: minLength(4)
    },
    phone: {
      required,
      numeric,
      minLength: minLength(10)
    },
    mail: {
      required,
      email
    },
    message: {
      required,
      minLength: minLength(5)
    }
  },

  methods: {
    setName(value) {
      this.name = value
      this.$v.name.$touch()
    },
    setPhone(value) {
      this.phone = value
      this.$v.phone.$touch()
    },
    setMail(value) {
      this.mail = value
      this.$v.mail.$touch()
    },
    setMessage(value) {
      this.message = value
      this.$v.message.$touch()
    },
    submit() {
      console.log(this.$v)
      this.$v.$touch()
      if (this.$v.$invalid) {
        this.submitStatus = 'ERROR'
      } else {
        // do your submit logic here
        this.submitStatus = 'PENDING'
        setTimeout(() => {
          this.submitStatus = 'OK'
        }, 500)
      }
    },
    toggleMenu (param) {
      let navMenu = document.querySelector('.nav-menu')
      switch (param) {
        case 'open':
          document.body.style.overflow = "hidden"
          navMenu.classList.remove('fadeOut')
          navMenu.classList.add('fadeIn')
          navMenu.style.display = 'block'
          navMenu.style.display = 'flex'
          break;
        case 'close':
          document.body.style.overflow = "auto"
          navMenu.classList.remove('fadeIn')
          navMenu.classList.add('fadeOut')
          setTimeout(() => { navMenu.style.display = 'none' }, 500)
          break;
      }
    },
    toggleForm (param) {
      let formMenu = document.querySelector('.form-menu')
      switch (param) {
        case 'open':
          document.body.style.overflow = "hidden"
          formMenu.classList.remove('fadeOut')
          formMenu.classList.add('fadeIn')
          formMenu.style.display = 'block'
          formMenu.style.display = 'flex'
          break;
        case 'close':
          document.body.style.overflow = "auto"
          formMenu.classList.remove('fadeIn')
          formMenu.classList.add('fadeOut')
          setTimeout(() => { formMenu.style.display = 'none' }, 500)
          break;
      }
    }
  },
  mounted() {
    scrollAnimation()

    document.addEventListener('scroll', () => {
      scrollAnimation()
    })

    function scrollAnimation() {
      let placeholder = [...document.querySelectorAll('.animated')]

      placeholder.map(item => {
        let animationName = item.getAttribute('data-animation')
        if (item.getBoundingClientRect().y < 700) {
          item.classList.add(animationName)
          item.style.opacity = '1'
        }
      })	
    }
  }
}
</script>


