<script>
import Button from '@/components/Button.vue'
import Row from '@/components/Row.vue'
import Hero from '@/components/Hero.vue'
import Slider from '@/components/Slider.vue'

export default {
  name: 'home',
  components: {
    Button,
    Row,
    Hero,
    Slider
  },
  methods: {
    toggleForm (param) {
      let formMenu = document.querySelector('.form-menu')
      switch (param) {
        case 'open':
          document.body.style.overflow = "hidden"
          formMenu.classList.remove('fadeOut')
          formMenu.classList.add('fadeIn')
          formMenu.style.display = 'block'
          formMenu.style.display = 'flex'
          break;
        case 'close':
          document.body.style.overflow = "auto"
          formMenu.classList.remove('fadeIn')
          formMenu.classList.add('fadeOut')
          setTimeout(() => { formMenu.style.display = 'none' }, 500)
          break;
      }
    }
  }
}
</script>

